<template>
	<view class="container">
		<watermark></watermark>
		<view class="fixedTop">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="change" height="110" bg-color="#03958C"
				inactive-color="#fff" active-color="#fff" :active-item-style="activeStyle"></u-tabs>
		</view>
		<template v-if="isshow">
			<view v-if="current==0" class="padiingTop90">
				<template v-if="nameList.length>0">
					<view class="approList" v-for="(item,index) in nameList" :key="index">
						<view class="apply_item" v-for="(item2,ind) in item.data" :key="ind">
							<view class="apply_top" v-if="ind == 0">
								<view>{{item2.name}}：<text class="yellow">{{item2.value}}</text></view>
								<view class="apply_btn" @click="goDet(item)"><text>审批</text></view>
							</view>
							<view v-else><text>{{item2.name}}：</text>{{item2.value}}</view>
						</view>
					</view>
				</template>
				<template v-else>
					<image class="noData" src="@/static/img/noData.png" mode=""></image>
				</template>
			</view>
			<view v-if="current==1" class="padiingTop90">
				<template v-if="nameList.length>0">
					<view class="approList" v-for="(item,index) in nameList" :key="index">
						<view class="apply_item" v-for="(item2,ind) in item.data" :key="ind">
							<view class="apply_top" v-if="ind == 0">
								<view>{{item2.name}}：<text class="yellow">{{item2.value}}</text></view>
								<view class="apply_btn" @click="goDet(item)"><text>审批</text></view>
							</view>
							<view v-else><text>{{item2.name}}：</text>{{item2.value}}</view>
						</view>
					</view>
				</template>
				<template v-else>
					<image class="noData" src="@/static/img/noData.png" mode=""></image>
				</template>
			</view>
			<view v-if="current==2" class="padiingTop90">
				<template v-if="nameList.length>0">
					<view class="approList" v-for="(item,index) in nameList" :key="index">
						<view class="apply_item" v-for="(item2,ind) in item.data" :key="ind">
							<view class="apply_top" v-if="ind == 0">
								<view>{{item2.name}}：<text class="yellow">{{item2.value}}</text></view>
								<view class="apply_btn" @click="goDet(item)"><text>审批</text></view>
							</view>
							<view v-else><text>{{item2.name}}：</text>{{item2.value}}</view>
						</view>
					</view>
				</template>
				<template v-else>
					<image class="noData" src="@/static/img/noData.png" mode=""></image>
				</template>
			</view>
		</template>
		
		<!-- <view class="footer" @click="goBack">返回</view> -->
		<!-- 审批弹窗 -->
		<u-popup v-model="showPoup" mode="center" width="600rpx" border-radius="14">
			<view class="switch_box">
				<view class="title">审批意见：</view>
				<view class="textareaBox"><textarea v-model="postData.opinion" placeholder-style="color:#999;font-size:28rpx" placeholder="请填写审批意见"/></view>
				<view class="footerBtn">
					<text class="cancel" @click="sure(false)">驳回</text>
					<text @click="sure(true)">同意</text>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import watermark from '@/components/watermark.vue';
	export default {
		data() {
			return {
				activeStyle: {
					fontSize: '36rpx'
				},
				isshow: false,
				list: [{
					name: '特殊折扣'
				}, {
					name: '客户移交'
				}, {
					name: '延期申请'
				}],
				current: 0,
				nameList:[], // 审批列表
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages:0, // 总页数
				loading: false,
				showPoup: false, // 是否显示弹窗
				postData: {
					id: '',
					opinion: '',
					flag: ''
				}
			}
		},
		onLoad(){
			this._getList()
		},
		methods:{
			// 获取客户列表
			_getList(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('3001', Object.assign({},{applyType:that.current == 0 ? 1 : (that.current == 1 ? 2 : 3)},that.page), function (res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.nameList = that.etype == 3 ? that.nameList.concat(res.data.data) : res.data.data
						that.totalPages = res.data.totalPages
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this._getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this._getList()
				}
			},
			// tab切换
			change(index) {
				this.current = index;
				this.nameList = []
				this._getList()
			},
			// 返回
			// goBack(){
			// 	uni.navigateBack({
			// 		delta:1
			// 	})
			// },
			// 去审批详情
			goDet(item){
				this.showPoup = true
				this.postData.opinion = ''
				this.postData.id = item.id
			},
			// 同意审批
			sure(index){
				let that = this;
				that.postData.flag = index
				Tools.showLoading('提交中')
				httpUtils.sendHttpRequest('3002', Object.assign({}, that.postData), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('操作成功')
						setTimeout(function() {
							that.showPoup = false
							that._getList()
						}, 500)
					}
				})
			}
		},
		components:{
			watermark
		}
	}
</script>

<style lang="scss">
	page{
		background-color: #f6f6f6;
	}
	@import '@/styles/poup.scss';
	.container{
		.padiingTop90{
			padding-top: 110rpx;
			.approList{
				margin: 26rpx;
				padding: 20rpx 30rpx;
				background: #fff;
				border-radius: 16rpx;
				position: relative;
				// .triangle{
				// 	position: absolute;
				// 	right: 0;
				// 	top: 0;
				// 	width: 0;
				// 	height: 0;
				// 	border-top: 70rpx solid #03958C;
				// 	border-bottom: 70rpx solid transparent;
				// 	border-left: 70rpx solid transparent;
				// 	text{
				// 		position: absolute;
				// 		top: -65rpx;
				// 		right: 10rpx;
				// 		color: #fff;
				// 		font-size: 30rpx;
				// 	}
				// }
				.applayPerson{
					display: flex;
					justify-content: space-between;
					align-items: center;
					.see{
						color: #03958C;
					}
				}
				.apply_item{
					padding: 12rpx 0;
					font-size: 28rpx;
					display: flex;
					text{
						color: #666;
						width: 150rpx;
						min-width: 150rpx;
						display: inline-block;
					}
					.reason{
						line-height: 46rpx;
					}
					.apply_top{
						width: 100%;
						display: flex;
						justify-content: space-between;
						align-items: center;
						border-bottom: 1rpx solid #e6e6e6;
						padding-bottom: 20rpx;
						.yellow{
							color: #F5B00C;
						}
					}
				}
				.apply_btn{
					text{
						min-width: 90rpx;
						width: 90rpx;
						height: 50rpx;
						line-height: 50rpx;
						text-align: center;
						color: #03958C;
						border: 2rpx solid #03958C;
						border-radius: 10rpx;
						font-size: 24rpx;
					}
				}
			}
		}
		// textarea样式更改
		.switch_box{
			.textareaBox{
				width: 100%;
				border: 1rpx solid #eee;
				padding: 30rpx 0;
			}
			textarea {
				width: 95%!important;
				height: 240rpx;
				font-size: 28rpx!important;
				padding-left: 20rpx;
			}
		}
	}
</style>
